
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif, 微软雅黑;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
html, body {
  padding: 0;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
.key {
  padding-right: 6px;
  line-height: 21px;
  letter-spacing: 1px;
}
.string {
  color: green;
}
.number {
  color: #ee422e;
}

