/* 动画 */
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 5s;
  transition: opacity 5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.list {
  padding-top: 5px;
}
/* select及其下方的接口宽度样式 */
.swagger-left {
  width: 21%;
  overflow: hidden auto;
  margin-top: 0px;
  position: fixed;
  height: 100%;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
/* 单选框 */
.form-control {
  display: block;
  width: 86%;
  margin: 0 auto 5px;
  padding: 4px 6px;
  border: 0;
  border-bottom: 1px solid #555;
  outline: none;
  height: 50px;
}
.nav-list {
  margin: 0;
  padding: 0;
}
.nav-list > li {
  display: block;
  margin: 0 auto 5px;
  border: 0;
  position: relative;
  border-left: 5px solid #fff;
  cursor: pointer;
  text-align: left;
  padding: 16px 0 16px 5%;
}
.nav-list > li:hover, .nav-list > li.active {
  background-color: #F3F8E4;
  color: #8ABF00;
  border-left: 5px solid #8ABF00;
}
/* 第一层接口列表名字 */
.nav-list-name {
  margin-right: 14px;
  float: left;
}
.nav-list-description {
  width: 172px;
  display: inline-block;
  text-align: left;
}
.nav-list-number {
  position: absolute;
  right: 27px;
  top: 16px;
  width: 20px;
  height: 20px;
  background-color: #FF3C43;
  border-radius: 10px;
  text-align: center;
  line-height: 20px;
  color: #fff;
  font-size: 14px;
}
/* 接口列表 */
.swagger-category {
  padding: 0;
  margin-left: 21%;
  width: 22%;
  margin-top: 0;
  position: fixed;
  height: 100%;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  overflow: hidden auto;
}
.swagger-category .category-li {
  text-align: left;
  margin-bottom: 5px;
  padding: 5px 10px;
}
.category-li .category-li-type {
  height: 20px;
  line-height: 20px;
}
.category-li .category-li-path {
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
  word-break: break-all;
}
.category-li .category-li-name {
  display: block;
  padding: 2px 0 0;
}
.notify .notify-msg {
  height: auto;
  max-width: 260px;
}
/*  tab切换选项 */
.tab-switch {
  position: relative;
  margin-left: 43%;
  margin-right: 51px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  white-space: nowrap;
  overflow-y: hidden;
  overflow-x: scroll;
  height: 50px;
}
.tab-switch > ul {
  font-size: 0;
  text-align: left;
}
/* 滚动条样式 */
.swagger-left::-webkit-scrollbar,
.tab-switch::-webkit-scrollbar,
.swagger-category::-webkit-scrollbar { /*滚动条整体样式*/
  width: 5px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 8px;
}
.swagger-left::-webkit-scrollbar-thumb,
.tab-switch::-webkit-scrollbar-thumb,
.swagger-category::-webkit-scrollbar-thumb { /*滚动条小方块*/
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #EBEBEB;
}
.swagger-left::-webkit-scrollbar-track,
.tab-switch::-webkit-scrollbar-track,
.swagger-category::-webkit-scrollbar-track { /*滚动条轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: #FFF;
}
.tab-switch > ul li {
  padding: 10px 6px 10px 15px;
  font-size: 14px;
  font-weight: 500;
  border-top: 1px solid #dbdbdb;
  cursor: pointer;
  display: inline-block;
  float: none;
  -webkit-box-shadow: 1px 1px 2px #e9e4e4;
          box-shadow: 1px 1px 2px #e9e4e4;
}
.tab-switch > ul li.active {
  background-color: #89BF05;
  color: #fff;
}
.tab-switch > ul li span {
  padding-right: 9px;
}
.tab-switch > ul li a {
  text-decoration: none;
  color: black;
}
.tab-switch > ul li.active span,
.tab-switch > ul li.active a {
  color: #fff;
}
.tab-switch > ul li a:hover,
.tab-switch > ul li.active a:hover {
  color: rgb(235, 91, 91);
}
/* 历史记录管理 */
.tab-switch .management {
  position: fixed;
  top: 0;
  right: 0;
}
.tab-switch .management a {
  text-decoration: none;
  color: #30ABF9;
  font-size: 30px;
  display: block;
  width: 36px;
  height: 36px;
  line-height: 37px;
  text-align: center;
  border-top: 1px solid #dbdbdb;
  -webkit-box-shadow: 1px 1px 2px #e9e4e4;
          box-shadow: 1px 1px 2px #e9e4e4;
  margin: 5px 15px auto auto;
}
.tab-switch .management ul {
  position: fixed;
  -webkit-transition: all .4s;
  transition: all .4s;
  z-index: 99;
  text-align: left;
  background: #30ABF9;
  padding: 10px;
  border-radius: 5px;
  color: #fff;
  right: 0;
}
.tab-switch .management ul li {
  cursor: pointer;
  margin: 4px auto;
  padding: 4px 10px;
  border-radius: 5px;
}
.tab-switch .management ul li:hover {
  background: rgb(57, 146, 208);
}
/* 响应式 */
@media screen and (min-width: 1600px) {
.swagger-left {
    width: 18%;
}
.swagger-category {
    width: 18%;
    margin-left: 18%;
}
.tab-switch {
    margin-left: 36%;
}
}

.login {
  position: fixed;
  background: #ccc;
  z-index: 999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.login .content {
  position: absolute;
  border-radius: 5px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  overflow: hidden;
}
.login .content header {
  background-color: #73CCFE;
  height: 45px;
  padding-left: 15px;
  text-align: left;
  line-height: 45px;
  color: #fff;
  font-weight: 600;
}
.login .content .input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.login .content .input {
  background: #fff;
  float: right;
  padding: 10px 20px 15px;
  min-width: 390px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 0 10px #ccc;
          box-shadow: 0 0 10px #ccc;
}
.login .content .input h3 {
  color: #73CCFE;
}
.login .content .input input {
  display: block;
  color: #2196F3;
  font-size: 16px;
  border: 1px solid #E9E9E9;
  width: 244px;
  outline: none;
  background: #F1F2F2;
  padding: 12px 22px;
  margin: 24px auto;
}
.login .content .input input::-webkit-input-placeholder {
  color: #73CCFE;
}
.login .content .input button {
  outline: none;
  cursor: pointer;
  width: 251px;
  height: 38px;
  padding: 5px 24px;
  border: 0;
  border-radius: 5px;
  background-color: #73CCFE;
  color: #fff;
  letter-spacing: 5px;
}
.login .content .input button:hover {
  background-color: #2196F3;
}


.about {
  -webkit-box-shadow: 1px 1px 5px #f3f4ef;
          box-shadow: 1px 1px 5px #f3f4ef;
  border: 1px solid #F3F4EF;
  margin-left: 43%;
  margin-right: 15px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.about > ul {
  overflow: hidden;
  border: 1px solid #ddd;
}
.about .head {
  font-size: 16px;
  font-weight: 700;
  background-color: #F8F8F8;
}
.about > ul li {
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
.about > ul li:last-child {
  border-bottom: 0;
}
.about > ul li > span {
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 8px 4px;
  border-right: 1px solid #ddd;
}
.about > ul li > span:first-child {
  width: 30%;
}
.about > ul li > span:last-child {
  border-right: 0;
  width: 70%;
}
.about > ul li.head > span {
  width: 100%;
}

/* 响应式 */
@media screen and (min-width: 1600px) {
.about {
    margin-left: 36%;
}
}



.copy-json {
  float: right;
  text-decoration: none;
  font-size: 12px;
}

/* 响应参数说明部分 */
.response-parameter .head {
  font-size: 16px;
  font-weight: 700;
  background-color: #F8F8F8;
}
.response-parameter > ul {
  overflow: hidden;
  border: 1px solid #ddd;
  border-bottom: 0;
}
.response-parameter > ul li {
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
.response-parameter > ul li:last-child {
  border-bottom: 0;
}
.response-parameter > ul li > span {
  width: 30%;
  float: left;
  padding: 8px 4px 999px;
  border-right: 1px solid #ddd;
  margin-bottom: -999px;
}
.response-parameter .head span {
  text-align: center;
}
.response-parameter > ul li > span:last-child {
  border-right: 0;
  width: 35%;
}

/*  响应状态码部分 */
.response-code li.head {
  font-size: 16px;
  font-weight: 700;
  background-color: #F8F8F8;
}
.response-code > ul {
  overflow: hidden;
  border: 1px solid #ddd;
}
.response-code > ul li {
  overflow: hidden;
  border-bottom: 1px solid #ddd;
  position: relative;
}
.response-code > ul li:last-child {
  border-bottom: 0;
}
.response-code > ul li > div,
.response-code li.head span {
  width: 50%;
  float: left;
  position: relative;
  padding-bottom: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.response-code li.head span {
  padding: 8px 4px;
  border-right: 1px solid #ddd;
}
.response-code > ul li > div:first-child,
.response-code li.head span:first-child {
  width: 15%;
}
.response-code > ul li > div:last-child,
.response-code li.head span:last-child {
  border-right: 0;
  width: 35%;
}
.response-code > ul li > div:last-child a {
  text-decoration: none;
  font-size: 12px;
  position: absolute;
  right: 0;
  top: 5px;
}
.response-code > ul li > div span {
  padding-bottom: 1000px;
  margin-bottom: -1000px;
  display: block;
  border-right: 1px solid #ddd;
  padding-top: 8px;
  padding-left: 4px;
  padding-right: 4px;
}
.response-code > ul li div span.format {
  white-space: pre-wrap;
}
.response-code > ul li > div:last-child span {
  border-right: 0;
}

/* 调试结果区域样式 */
.debugging-result {
  text-align: left;
  font-size: 0;
}
.debugging-result > span {
  font-size: 14px;
  display: inline-block;
  cursor: pointer;
  padding: 10px 15px;
  border: 1px solid #EBEBEB;
  position: relative;
  border-right: 0;
  bottom: -1px;
}
.debugging-result > b {
  font-size: 12px;
  float: right;
  color: #858585;
  margin-top: 12px;
}
.debugging-result > b a {
  text-decoration: none;
  color: #2E8BF0;
}
.debugging-result > span:last-of-type {
  border-right: 1px solid #EBEBEB;
}
.debugging-result > span.active {
  -webkit-box-shadow: 0 3px #89BF05 inset;
          box-shadow: 0 3px #89BF05 inset;
  color: #89BF05;
  border-bottom: 1px solid transparent;
  border-bottom: 1px solid #FFFFFF;
}
.debugging-curl > div {
  color: #393939;
  font-size: 13px;
  white-space: pre;
  overflow-x: auto;
  padding: 15px;
}
.result-content {
  border-top: 1px solid #EBEBEB;
  padding-top: 15px;
  font-size: 16px;
}
.result-content > div {
  border: 1px solid #ddd;
  min-height: 210px;
  font-size: 16px;
  text-align: left;
  padding: 17px 15px 15px;
}
.result-content .content li {
  white-space: pre-wrap;
  color: #1A1A1A;
  font-size: 18px;
}
.debugging-header > ul {
  border: 1px solid #ddd;
}
.debugging-header li {
  border-bottom: 1px solid #ddd;
}
.debugging-header li > span {
  padding: 10px 15px 10px 5px;
  display: inline-block;
}
.debugging-header li > span:first-child {
  font-weight: 700;
  width: 38%;
  border-right: 1px solid #ddd;
}
.debugging-header li > span:last-child {
  width: 45%;
}
.debugging-header .head {
  font-weight: 700;
}

/* 请求参数表格 */
.table-tr {
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  word-wrap: break-word;
}
.table-head {
  font-size: 16px;
  font-weight: 700;
  background-color: #F8F8F8;
}
.table-head .table-td {
  padding: 8px 4px;
}
.table-td {
  border-right: 1px solid #ddd;
  width: 15%;
  float: left;
  padding: 8px 4px;
  text-align: left;
}
.table-td:last-child {
  border-right: 0;
}

/* 接口详细信息列表 */
.swagger-content, .debugging-content {
  border-top: 1px solid #dbdbdb;
  padding: 20px 10px;
}
.content-list {
  text-align: left;
